import { motion } from "framer-motion";
import React, { useEffect, useRef, useState, useMemo } from "react";
import { themes } from "prism-react-renderer";
import AnimatedWord from "./AnimatedWord";
import PhotoCorners from "./PhotoCorners";
import "./SingleQuestionAnimation.css";
import { useTheme } from "./ThemeContext";
import RichTextRenderer from "./RichTextRenderer";
import { stripCustomTags } from "./utils";
import CodeHighlighter from "./CodeHighlighter";

// --- Configuration ---
const DEFAULT_ANIMATION_TYPE = "svg-curve";
const RESIZE_ANIMATION_TYPE = "spring";
const delayBeforeStartMeasurement = 100;
const resizeDebounceDelay = 250;
const HINT_LENGTH = 3;
const GRACE_PERIOD_MS = 70;

// --- Regex and Constants ---
const BLANK_REGEX = /(_BF_\d+)/g;
const BLANK_TEST_REGEX = /^_BF_\d+$/;
const MARKER_ONLY_REGEX = /\s*\/\/\*\*\s*/g;
const RICH_TEXT_SOURCE =
  /<pre><code(?:\s+lang=['"](.*?)['"])?>(.*?)<\/code><\/pre>|<code>(.*?)<\/code>|<span\s+style=\{\{(.*?)\}\}>(.*?)<\/span>|(\p{Emoji_Presentation}|\p{Emoji}\uFE0F)/
    .source;
const BLANK_SOURCE = BLANK_REGEX.source;
const COMBINED_PARSING_REGEX = new RegExp(
  `${RICH_TEXT_SOURCE}|${BLANK_SOURCE}`,
  "gus",
);
const PRE_LANG_INDEX = 1;
const PRE_CONTENT_INDEX = 2;
const BLANK_CAPTURE_GROUP_INDEX = 7;

const escapeRegExp = (string) => string.replace(/[.*+?^${}()|[\]\\]/g, "\\$&");

const containerVariants = {
  hidden: { opacity: 0, scale: 0.98 },
  visible: {
    opacity: 1,
    scale: 1,
    transition: { duration: 1.5, ease: "easeInOut" },
  },
};

const isElementInViewport = (el) => {
  if (!el) return false;
  const rect = el.getBoundingClientRect();
  return (
    rect.top >= 0 &&
    rect.left >= 0 &&
    rect.bottom <=
      (window.innerHeight || document.documentElement.clientHeight) &&
    rect.right <= (window.innerWidth || document.documentElement.clientWidth)
  );
};

const smoothScrollTo = (element, duration) => {
  const targetPosition =
    element.getBoundingClientRect().top +
    window.pageYOffset -
    window.innerHeight / 2 +
    element.clientHeight / 2;
  const startPosition = window.pageYOffset;
  const distance = targetPosition - startPosition;
  let startTime = null;

  const easeInOutQuad = (t, b, c, d) => {
    t /= d / 2;
    if (t < 1) return (c / 2) * t * t + b;
    t--;
    return (-c / 2) * (t * (t - 2) - 1) + b;
  };

  const animation = (currentTime) => {
    if (startTime === null) startTime = currentTime;
    const timeElapsed = currentTime - startTime;
    const run = easeInOutQuad(timeElapsed, startPosition, distance, duration);
    window.scrollTo(0, run);
    if (timeElapsed < duration) {
      requestAnimationFrame(animation);
    }
  };

  requestAnimationFrame(animation);
};

const SingleQuestionAnimation = ({
  data,
  hintuse = false,
  sparseFlashyToggle = false,
  onAllAnimationsComplete = () => {},
  isViewLocked = false,
  animationIntervals,
  currentPartNumber,
}) => {
  // --- STEP 1: CALL ALL HOOKS UNCONDITIONALLY ---
  const { mode } = useTheme();
  const isDarkMode = mode === "dark";
  const containerRef = useRef(null);
  const blankRefs = useRef({});
  const [fillTargets, setFillTargets] = useState({});
  const [containerRect, setContainerRect] = useState(null);
  const [windowTrigger, setWindowTrigger] = useState(0);
  const [hasResized, setHasResized] = useState(false);
  const [animatedKeys, setAnimatedKeys] = useState(new Set());
  const [completedKeys, setCompletedKeys] = useState({});
  const [latestAnimatedKey, setLatestAnimatedKey] = useState(null);
  const isInitialScroll = useRef(true);

  useEffect(() => {
    isInitialScroll.current = true;
  }, [data]);

  const handleContainerClick = (event) => {
    const clickX = event.clientX;
    const clickY = event.clientY;
    let clickedBlankKey = null;
    for (const key in blankRefs.current) {
      const element = blankRefs.current[key];
      if (element) {
        const rect = element.getBoundingClientRect();
        if (
          clickX >= rect.left &&
          clickX <= rect.right &&
          clickY >= rect.top &&
          clickY <= rect.bottom
        ) {
          clickedBlankKey = key;
          break;
        }
      }
    }
    if (clickedBlankKey && !completedKeys[clickedBlankKey]) {
      setCompletedKeys((prevKeys) => ({
        ...prevKeys,
        [clickedBlankKey]: true,
      }));
    }
  };

  useEffect(() => {
    setCompletedKeys({});
    setAnimatedKeys(new Set());
    setFillTargets({});
    setLatestAnimatedKey(null);
  }, [data]);

  const handleAnimationEnd = (key) =>
    setCompletedKeys((prevKeys) => ({ ...prevKeys, [key]: true }));

  const parsedFills = useMemo(() => {
    if (!data || !data.Fill) return [];
    const fills = [];
    const fillString = data.Fill;
    const keyRegex = /(_BF_\d+)/g;
    let lastIndex = 0;
    let match;
    while ((match = keyRegex.exec(fillString)) !== null) {
      const textChunk = fillString.substring(lastIndex, match.index);
      const key = match[1];
      const rawText = textChunk.replace(/^,/, "").trim();
      fills.push({ rawText, key });
      lastIndex = match.index + key.length;
    }
    return fills.map(({ rawText, key }) => {
      const uiText = rawText.replace(MARKER_ONLY_REGEX, "").trim();
      return { text: uiText, rawTextForRegex: rawText, key };
    });
  }, [data?.Fill]);

  const singlePartInterval = useMemo(() => {
    const DEFAULT_INTERVAL = 4000;
    if (
      animationIntervals &&
      currentPartNumber != null &&
      animationIntervals.length > currentPartNumber
    ) {
      return animationIntervals[currentPartNumber] || DEFAULT_INTERVAL;
    }
    return DEFAULT_INTERVAL;
  }, [animationIntervals, currentPartNumber]);

  const animationStartTimes = useMemo(() => {
    const startTimes = [];
    for (let i = 0; i < parsedFills.length; i++) {
      const delay = GRACE_PERIOD_MS + i * singlePartInterval;
      startTimes.push(delay);
    }
    return startTimes;
  }, [singlePartInterval, parsedFills.length]);

  const measurePositions = () => {
    if (!containerRef.current) return;
    setContainerRect(containerRef.current.getBoundingClientRect());
    const targets = {};
    parsedFills.forEach(({ key }) => {
      const el = blankRefs.current[key];
      if (el) {
        targets[key] = { x: el.offsetLeft, y: el.offsetTop };
      }
    });
    if (Object.keys(targets).length > 0) {
      setFillTargets(targets);
    }
  };

  useEffect(() => {
    if (
      Object.keys(fillTargets).length > 0 &&
      Object.keys(completedKeys).length === parsedFills.length
    ) {
      onAllAnimationsComplete();
    }
  }, [completedKeys, fillTargets, parsedFills.length, onAllAnimationsComplete]);
  useEffect(() => {
    const timeoutId = setTimeout(measurePositions, delayBeforeStartMeasurement);
    return () => clearTimeout(timeoutId);
  }, [data?.Ans, windowTrigger]);
  useEffect(() => {
    let debounceTimeoutId = null;
    const handleResize = () => {
      clearTimeout(debounceTimeoutId);
      debounceTimeoutId = setTimeout(() => {
        setHasResized(true);
        setWindowTrigger((prev) => prev + 1);
      }, resizeDebounceDelay);
    };
    window.addEventListener("resize", handleResize);
    return () => {
      window.removeEventListener("resize", handleResize);
      clearTimeout(debounceTimeoutId);
    };
  }, []);

  useEffect(() => {
    // This effect is now safe, because on the initial render where props might be missing,
    // `parsedFills` will be an empty array, so no timers will be set.
    const timerIDs = [];
    parsedFills.forEach(({ key }, index) => {
      const delay = animationStartTimes[index];
      const id = setTimeout(() => {
        setAnimatedKeys((prevKeys) => new Set(prevKeys).add(key));
        setLatestAnimatedKey(key);
      }, delay);
      timerIDs.push(id);
    });
    return () => timerIDs.forEach((id) => clearTimeout(id));
  }, [parsedFills, animationStartTimes]);

  useEffect(() => {
    const INITIAL_LOAD_SCROLL_DELAY_MS = 4000;
    const SHORT_QUESTION_SCROLL_DELAY_MS = 3000;
    let scrollTimerId;
    const performScroll = () => {
      if (isViewLocked) {
        return;
      }
      if (!latestAnimatedKey || !containerRef.current) {
        return;
      }
      const bfId = latestAnimatedKey.split("_BF_")[1];
      if (!bfId) return;
      const targetElement = containerRef.current.querySelector(
        `[data-bf-id="${bfId}"]`,
      );
      if (!targetElement) return;
      if (isElementInViewport(targetElement)) {
        return;
      }
      smoothScrollTo(targetElement, 2500);
    };
    let delay = 0;
    if (isInitialScroll.current) {
      delay = INITIAL_LOAD_SCROLL_DELAY_MS;
      isInitialScroll.current = false;
    } else {
      const totalBlanks = parsedFills.length;
      delay = totalBlanks <= 2 ? SHORT_QUESTION_SCROLL_DELAY_MS : 0;
    }
    scrollTimerId = setTimeout(performScroll, delay);
    return () => clearTimeout(scrollTimerId);
  }, [latestAnimatedKey, isViewLocked, parsedFills.length, data]);

  const currentAnimationType = hasResized
    ? RESIZE_ANIMATION_TYPE
    : DEFAULT_ANIMATION_TYPE;
  const renderedQues = useMemo(
    () => data?.Ques?.replace(/_BF_\d+/g, "") || "",
    [data?.Ques],
  );
  const initialWordPositions = useMemo(() => {
    const startY = containerRect ? containerRect.height + 10 : 180;
    return parsedFills.map((_, index) => ({ x: 10 + index * 100, y: startY }));
  }, [parsedFills, containerRect]);
  const cleanedAns = useMemo(() => {
    if (!data?.Ans) return "";
    let processedAns = data.Ans;
    parsedFills.forEach(({ rawTextForRegex, key }) => {
      const textToFindInAns = rawTextForRegex
        .replace(MARKER_ONLY_REGEX, "")
        .trim();
      if (textToFindInAns) {
        const tautologyRegex = new RegExp(
          escapeRegExp(textToFindInAns) + key,
          "g",
        );
        processedAns = processedAns.replace(tautologyRegex, key);
      }
    });
    processedAns = processedAns.replace(MARKER_ONLY_REGEX, " ");
    return processedAns;
  }, [data?.Ans, parsedFills]);

  const renderedAnswerBody = useMemo(() => {
    const elements = [];
    if (!cleanedAns) return elements;
    const FADE_OVERLAP_MS = 3700;
    const fadeDurationMs = singlePartInterval + FADE_OVERLAP_MS;
    const fadeDurationSec = fadeDurationMs / 1000;
    const createBlankSpan = (key, index) => {
      if (!blankRefs.current[key]) blankRefs.current[key] = null;
      const fillData = parsedFills.find((f) => f.key === key);
      const isCompleted = completedKeys[key];
      const plainTextForSizing = stripCustomTags(fillData?.text || "");
      const basePositionStyle = {
        position: "absolute",
        top: 0,
        left: 0,
        whiteSpace: "nowrap",
        lineHeight: "inherit",
        marginLeft: "3px",
      };
      const hintContent =
        hintuse && fillData ? (
          <div
            style={{
              ...basePositionStyle,
              transition: `opacity ${fadeDurationSec}s ease-out`,
              opacity: isCompleted || animatedKeys.has(key) ? 0 : 1,
              visibility: isCompleted ? "hidden" : "visible",
              color: "Silver",
              fontWeight: "500",
              verticalAlign: "bottom",
            }}
          >
            {" "}
            {plainTextForSizing.substring(0, HINT_LENGTH)}{" "}
          </div>
        ) : null;
      const finalContent = fillData ? (
        <div
          style={{
            ...basePositionStyle,
            transition: "opacity 0.3s ease-in-out",
            opacity: isCompleted ? 1 : 0,
            color: sparseFlashyToggle ? "DodgerBlue" : "DarkGray",
            fontSize: "inherit",
            fontWeight: "inherit",
          }}
        >
          {" "}
          <RichTextRenderer text={fillData.text} />{" "}
        </div>
      ) : null;
      const bfId = key.split("_BF_")[1];
      return (
        <span
          key={`${key}-${index}`}
          ref={(el) => {
            blankRefs.current[key] = el;
          }}
          data-bf-id={bfId}
          style={{
            display: "inline-block",
            position: "relative",
            minWidth: "15px",
            borderBottom: "1px solid #184012",
            margin: "0",
            padding: "0 2px",
            verticalAlign: "bottom",
            textAlign: "left",
            color: "transparent",
          }}
        >
          {plainTextForSizing}
          {hintContent}
          {finalContent}
        </span>
      );
    };
    let lastIndex = 0;
    let match;
    COMBINED_PARSING_REGEX.lastIndex = 0;
    while ((match = COMBINED_PARSING_REGEX.exec(cleanedAns)) !== null) {
      if (match.index > lastIndex) {
        elements.push(
          <RichTextRenderer
            key={`text-${lastIndex}`}
            text={cleanedAns.substring(lastIndex, match.index)}
          />,
        );
      }
      const fullMatch = match[0];
      const blankKey = match[BLANK_CAPTURE_GROUP_INDEX];
      const preCodeContent = match[PRE_CONTENT_INDEX];
      if (blankKey) {
        elements.push(createBlankSpan(blankKey, match.index));
      } else if (preCodeContent !== undefined) {
        const language = match[PRE_LANG_INDEX];
        const lines = preCodeContent.split("\n");
        const theme = isDarkMode ? themes.vsDark : themes.github;
        elements.push(
          <pre
            key={`pre-${match.index}`}
            style={{
              ...theme.plain,
              fontFamily: "inherit",
              fontSize: "inherit",
              whiteSpace: "pre-wrap",
              wordBreak: "break-all",
            }}
            className="code-block-container composed"
          >
            {lines.map((line, i) => (
              <div key={i} className="code-line" style={{ display: "flex" }}>
                <span
                  className="line-number"
                  style={{
                    flexShrink: 0,
                    textAlign: "right",
                    paddingRight: "1em",
                    userSelect: "none",
                    opacity: 0.5,
                  }}
                >
                  {i + 1}
                </span>
                <span className="line-content" style={{ flexGrow: 1 }}>
                  {line.split(BLANK_REGEX).map((part, j) => {
                    if (BLANK_TEST_REGEX.test(part)) {
                      return createBlankSpan(part, `${match.index}-${i}-${j}`);
                    }
                    return (
                      <CodeHighlighter
                        key={`${match.index}-${i}-${j}-code`}
                        code={part}
                        language={language}
                        inline={true}
                      />
                    );
                  })}
                </span>
              </div>
            ))}
          </pre>,
        );
      } else {
        elements.push(
          <RichTextRenderer key={`rich-${match.index}`} text={fullMatch} />,
        );
      }
      lastIndex = COMBINED_PARSING_REGEX.lastIndex;
    }
    if (lastIndex < cleanedAns.length) {
      elements.push(
        <RichTextRenderer
          key={`text-${lastIndex}`}
          text={cleanedAns.substring(lastIndex)}
        />,
      );
    }
    return elements;
  }, [
    cleanedAns,
    hintuse,
    animatedKeys,
    completedKeys,
    parsedFills,
    isDarkMode,
    sparseFlashyToggle,
    singlePartInterval,
  ]);

  // --- STEP 2: RENDER A LOADING STATE IF DATA ISN'T READY ---
  // It is now safe to return early because all hooks have been called.
  if (!data || !animationIntervals || currentPartNumber == null) {
    return (
      <motion.div
        variants={containerVariants}
        initial="hidden"
        animate="visible"
      >
        <h3>Answer</h3>
        <div
          style={{ position: "relative", minHeight: "220px", margin: "1rem" }}
        >
          Loading animation...
        </div>
      </motion.div>
    );
  }

  // --- STEP 3: RENDER THE FULL COMPONENT ---
  const containerClassName = isDarkMode
    ? "photo-container dark-mode"
    : "photo-container";

  return (
    <motion.div variants={containerVariants} initial="hidden" animate="visible">
      <h3>Question</h3>
      <p>{renderedQues}</p>
      <h3>Answer</h3>
      <div
        ref={containerRef}
        onClick={handleContainerClick}
        className={containerClassName}
        style={{
          position: "relative",
          minHeight: "220px",
          border: "1.5px solid #ccc",
          borderRadius: "8px",
          padding: "10px",
          overflow: "hidden",
          cursor:
            Object.keys(completedKeys).length < parsedFills.length
              ? "pointer"
              : "default",
          margin: "1rem",
        }}
      >
        <PhotoCorners />
        <div style={{ whiteSpace: "pre-wrap", margin: 0, lineHeight: "1.8" }}>
          {renderedAnswerBody}
        </div>
        {parsedFills.map(({ text, key }, index) => {
          const target = fillTargets[key];
          if (!target || completedKeys[key]) return null;
          return (
            <AnimatedWord
              key={`${key}-anim`}
              text={text}
              textColor={sparseFlashyToggle ? "DodgerBlue" : "DarkGray"}
              targetX={target.x}
              targetY={target.y}
              initialX={initialWordPositions[index].x}
              initialY={initialWordPositions[index].y}
              startDelay={animationStartTimes[index]}
              animationType={currentAnimationType}
              containerWidth={containerRect?.width}
              containerHeight={containerRect?.height}
              onAnimationEnd={() => handleAnimationEnd(key)}
              fontSize="inherit"
            />
          );
        })}
      </div>
    </motion.div>
  );
};

export default SingleQuestionAnimation;
