// src/RichTextRenderer.js
import React from 'react';
import { motion } from 'framer-motion';

// --- UPGRADED HELPER FUNCTION: This now understands gradients ---
const parseStyleString = (styleStr) => {
  const styleObject = {};
  if (!styleStr) return styleObject;

  const styleRegex = /(\w+)\s*:\s*'(.*?)'/g;
  let match;
  let hasGradient = false;

  while ((match = styleRegex.exec(styleStr)) !== null) {
    const key = match[1].trim();
    const value = match[2].trim();

    if (key === 'gradient') {
      hasGradient = true;
      styleObject.backgroundImage = `linear-gradient(${value})`;
      styleObject.WebkitBackgroundClip = 'text';
      styleObject.backgroundClip = 'text';
    } else {
      styleObject[key] = value;
    }
  }

  if (hasGradient) {
    styleObject.color = 'transparent';
  }

  return styleObject;
};

const PARSE_REGEX = /<span\s+style=\{\{(.*?)\}\}>(.*?)<\/span>|(\p{Emoji_Presentation}|\p{Emoji}\uFE0F)/gu;

const emojiVariants = {
  hover: {
    scale: 1.4,
    rotate: 15,
    transition: { type: 'spring', stiffness: 400, damping: 10 },
  },
};

const RichTextRenderer = ({ text }) => {
  if (!text || typeof text !== 'string') {
    return null;
  }

  const elements = [];
  let lastIndex = 0;
  let match;

  while ((match = PARSE_REGEX.exec(text)) !== null) {
    if (match.index > lastIndex) {
      elements.push(text.substring(lastIndex, match.index));
    }
    const [fullMatch, styleString, innerText, emoji] = match;
    if (styleString !== undefined && innerText !== undefined) {
      const style = parseStyleString(styleString);
      elements.push(
        <span key={match.index} style={style}>
          {innerText}
        </span>
      );
    } 
    else if (emoji) {
      elements.push(
        <motion.span
          key={match.index}
          variants={emojiVariants}
          whileHover="hover"
          style={{ display: 'inline-block', cursor: 'pointer' }}
        >
          {emoji}
        </motion.span>
      );
    }
    lastIndex = PARSE_REGEX.lastIndex;
  }
  if (lastIndex < text.length) {
    elements.push(text.substring(lastIndex));
  }
  return <>{elements.map((el, i) => <React.Fragment key={i}>{el}</React.Fragment>)}</>;
};

export default RichTextRenderer;