import { motion } from "framer-motion";
import React, { useState, useEffect, useMemo, useRef } from "react";
import { useTheme } from "./ThemeContext";
import "./App.css";
import CryptoJS from "crypto-js";
import {
  Switch,
  Button,
  Card,
  CardContent,
  Typography,
  Box,
  MenuItem,
  Select,
  FormControl,
  InputLabel,
  CircularProgress,
  ThemeProvider,
  createTheme,
  CssBaseline,
} from "@mui/material";
import ThemeToggleButton from "./ThemeToggleButton";
import QuestionFillAnimation from "./QuestionFillAnimation";
import { useHotkeys } from "react-hotkeys-hook";

function App() {
  const [questions, setQuestions] = useState([]);
  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [selectedSection, setSelectedSection] = useState("01");
  const [selectedPart, setSelectedPart] = useState("001");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);
  const { mode } = useTheme();

  const [isHovering, setIsHovering] = useState(false);
  const [autoskipDirection, setAutoskipDirection] = useState("Next");

  const [countdown, setCountdown] = useState(0);
  const timerIdRef = useRef(null);
  const [isSparseFlashyToggle, setIsSparseFlashyToggle] = useState(true);

  const [isViewLocked, setIsViewLocked] = useState(false);
  const unlockViewTimerRef = useRef(null);
  const sections = [
    {
      value: "01",
      label: "Group 1",
      parts: 7,
      type: "fill-in",
      auto_skip_to_implement: true,
      auto_skip_pausable: true,
      autoskip_to_work_with_parts: [1, 2, 5, 6, 7],
      animationInterval_speed_of__parts_1based: [
        0.0, 10500, 10500, 10500, 10500, 10500, 10500, 10500,
      ],
      autoskip_GoingDirection: "Next",
    },
    {
      value: "02",
      label: "Group 2",
      parts: 2,
      type: "fill-in",
      auto_skip_to_implement: false,
      auto_skip_pausable: false,
      autoskip_to_work_with_parts: [1, 2],
      animationInterval_speed_of__parts_1based: [0.0, 6500, 3500],
      autoskip_GoingDirection: "Next",
    },
    {
      value: "03",
      label: "Group 3",
      parts: 5,
      type: "fill-in",
      auto_skip_to_implement: true,
      auto_skip_pausable: true,
      autoskip_to_work_with_parts: [1, 2, 3, 4, 5],
      animationInterval_speed_of__parts_1based: [
        0.0, 10500, 10500, 10500, 10500, 10500,
      ],
      autoskip_GoingDirection: "Next",
    },
    {
      value: "04",
      label: "Group 4",
      parts: 5,
      type: "fill-in",
      auto_skip_to_implement: true,
      auto_skip_pausable: true,
      autoskip_to_work_with_parts: [1, 2, 3, 4, 5],
      animationInterval_speed_of__parts_1based: [
        0.0, 10500, 10500, 10500, 10500, 10500,
      ],
      autoskip_GoingDirection: "Next",
    },
  ];

  const partsForSelectedSection = selectedSection
    ? Array.from(
        { length: sections.find((s) => s.value === selectedSection).parts },
        (_, index) => ({
          value: (index + 1).toString().padStart(3, "0"),
          label: `Part ${index + 1}`,
        }),
      )
    : [];

  const BASE_AUTOSKIP_DELAY_MS = 10000;
  const MS_PER_BLANK = 10100;
  const MS_PER_CHARACTER = 20;
  const MIN_AUTOSKIP_DELAY_MS = 15000;
  const MAX_AUTOSKIP_DELAY_MS = 690000;

  const loadQuestions = async (section, part) => {
    setLoading(true);
    setError(false);
    try {
      const encryptedModule = await import(
        `./encryptedQuestions${section}${part}.json`
      );
      const secretKey = "your-secret-key";
      const decryptedData = CryptoJS.AES.decrypt(
        encryptedModule.default.data,
        secretKey,
      ).toString(CryptoJS.enc.Utf8);
      setQuestions(JSON.parse(decryptedData));
      setCurrentQuestion(0);
      const currentSectionConfig = sections.find((s) => s.value === section);
      if (currentSectionConfig) {
        setAutoskipDirection(
          currentSectionConfig.autoskip_GoingDirection || "Next",
        );
      }
    } catch (err) {
      console.error("Error loading or decrypting questions:", err);
      setError(true);
      setQuestions([]);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (selectedPart && selectedSection) {
      loadQuestions(selectedSection, selectedPart);
    }
  }, [selectedPart, selectedSection]);

  useEffect(() => {
    clearTimeout(timerIdRef.current);
    const currentSection = sections.find(
      (section) => section.value === selectedSection,
    );
    const currentPartNumber = selectedPart ? parseInt(selectedPart, 10) : -1;
    const isAutoSkipActive =
      currentSection?.auto_skip_to_implement &&
      currentSection?.autoskip_to_work_with_parts?.includes(
        currentPartNumber,
      ) &&
      questions.length > 1;
    if (!isAutoSkipActive) {
      setCountdown(0);
      return;
    }
    const currentQuestionData = questions[currentQuestion];
    if (!currentQuestionData) return;
    const answerText = currentQuestionData.Ans || "";
    const placeholderRegex = /_BF_\d+/g;
    const matches = answerText.match(placeholderRegex);
    const blankCount = matches ? matches.length : 0;
    const readingTextLength = answerText.replace(placeholderRegex, "").length;
    const calculatedDelay =
      BASE_AUTOSKIP_DELAY_MS +
      blankCount * MS_PER_BLANK +
      readingTextLength * MS_PER_CHARACTER;
    const finalDelay = Math.max(
      MIN_AUTOSKIP_DELAY_MS,
      Math.min(calculatedDelay, MAX_AUTOSKIP_DELAY_MS),
    );
    setCountdown(Math.round(finalDelay / 1000));
  }, [currentQuestion, questions, selectedPart, selectedSection]);

  useEffect(() => {
    if (isHovering || countdown <= 0) {
      clearTimeout(timerIdRef.current);
      return;
    }
    timerIdRef.current = setTimeout(() => {
      setCountdown((prev) => prev - 1);
    }, 1000);
    if (countdown === 1) {
      setTimeout(() => {
        setCurrentQuestion((prevIndex) => {
          if (autoskipDirection === "Next") {
            if (prevIndex < questions.length - 1) {
              return prevIndex + 1;
            } else {
              setAutoskipDirection("Previous");
              return prevIndex - 1;
            }
          } else {
            if (prevIndex > 0) {
              return prevIndex - 1;
            } else {
              setAutoskipDirection("Next");
              return prevIndex + 1;
            }
          }
        });
      }, 1000);
    }
    return () => clearTimeout(timerIdRef.current);
  }, [countdown, isHovering, questions.length, autoskipDirection]);

  useHotkeys("alt+0", () => handleSparseFlashyToggle());
  useHotkeys("alt+1,arrowright", () => handleNext());
  useHotkeys("alt+Q", () => handleReturnToPart());
  useHotkeys("alt+S", () => handleReturnToSection());
  useHotkeys("alt+2,arrowleft", () => handlePrevious());

  const handleSparseFlashyToggle = () =>
    setIsSparseFlashyToggle((prev) => !prev);
  const handleReturnToPart = () => {
    setSelectedPart("");
    setQuestions([]);
  };
  const handleReturnToSection = () => {
    setSelectedSection("");
    setSelectedPart("");
    setQuestions([]);
  };
  const handleNext = () => {
    if (currentQuestion < questions.length - 1) {
      setCurrentQuestion(currentQuestion + 1);
    }
  };
  const handlePrevious = () => {
    if (currentQuestion > 0) {
      setCurrentQuestion(currentQuestion - 1);
    }
  };

  const theme = useMemo(
    () =>
      createTheme({
        palette: {
          mode,
          primary: { main: "#1976d2" },
          secondary: { main: "#9c27b0" },
        },
      }),
    [mode],
  );

  const currentSectionConfig = sections.find(
    (s) => s.value === selectedSection,
  );
  const isAutoSkipActiveForThisPart =
    currentSectionConfig?.autoskip_to_work_with_parts?.includes(
      parseInt(selectedPart, 10),
    );

  if (loading)
    return (
      <Box
        display="flex"
        justifyContent="center"
        alignItems="center"
        minHeight="100vh"
      >
        <CircularProgress />
      </Box>
    );
  if (questions.length === 0 && selectedPart && !error)
    return (
      <Box
        display="flex"
        justifyContent="center"
        alignItems="center"
        minHeight="100vh"
      >
        <CircularProgress />
      </Box>
    );

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 1 }}
      >
        <Box sx={{ position: "absolute", top: 16, right: 16, zIndex: 1300 }}>
          <ThemeToggleButton />
        </Box>
        <Box
          display="flex"
          flexDirection="column"
          justifyContent="center"
          alignItems="center"
          minHeight="100vh"
          gap={2}
        >
          {!selectedSection ? (
            <Card sx={{ width: 500, p: 3 }}>
              <CardContent>
                <Typography variant="h5" gutterBottom>
                  Select a Group
                </Typography>
                <FormControl fullWidth>
                  <InputLabel id="select-section-label">
                    Select Group
                  </InputLabel>
                  <Select
                    labelId="select-section-label"
                    value={selectedSection}
                    onChange={(e) => setSelectedSection(e.target.value)}
                    label="Select Section"
                  >
                    {sections.map((section) => (
                      <MenuItem key={section.value} value={section.value}>
                        {section.label}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </CardContent>
            </Card>
          ) : !selectedPart ? (
            <Card sx={{ width: 500, p: 3 }}>
              <CardContent>
                <Typography variant="h5" gutterBottom>
                  Select a Part from Group {selectedSection}
                </Typography>
                <FormControl fullWidth>
                  <InputLabel id="select-part-label">Select Part</InputLabel>
                  <Select
                    labelId="select-part-label"
                    value={selectedPart}
                    onChange={(e) => setSelectedPart(e.target.value)}
                    label="Select Part"
                  >
                    {partsForSelectedSection.map((part) => (
                      <MenuItem key={part.value} value={part.value}>
                        {part.label}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </CardContent>
            </Card>
          ) : error ? (
            <Box
              display="flex"
              flexDirection="column"
              justifyContent="center"
              alignItems="center"
            >
              <Typography color="error">Error loading questions.</Typography>
              <Button onClick={handleReturnToPart} sx={{ mt: 2 }}>
                Go Back
              </Button>
            </Box>
          ) : (
            <>
              <Card
                sx={{
                  width: { xs: "95%", sm: "80%", md: "700px" },
                  maxWidth: "90vw",
                  p: { xs: 1, sm: 2, md: 3 },
                  overflowY: "auto",
                }}
              >
                <CardContent>
                  <Typography variant="h6" gutterBottom>
                    Question {currentQuestion + 1}/{questions.length}
                    <span
                      style={{
                        fontSize: "0.8rem",
                        marginLeft: "8px",
                        color: "gray",
                      }}
                    >
                      (g{selectedSection}/p{selectedPart})
                    </span>
                    {currentSectionConfig?.auto_skip_pausable &&
                      currentSectionConfig?.auto_skip_to_implement &&
                      isAutoSkipActiveForThisPart && (
                        <span
                          onMouseEnter={() => setIsHovering(true)}
                          onMouseLeave={() => setIsHovering(false)}
                          style={{
                            cursor: "pointer",
                            fontSize: "0.8rem",
                            color: "gray",
                            userSelect: "none",
                          }}
                          className="auto-n_seconds"
                        >
                          - Hover here to pause AutoSkip ({countdown}s)
                        </span>
                      )}
                    <Switch
                      checked={isSparseFlashyToggle}
                      onChange={handleSparseFlashyToggle}
                      color="primary"
                      name="BlendToggle"
                    />
                    <span style={{ fontSize: "50%" }}>Distinct (Alt+ 0)</span>
                  </Typography>

                  <Typography
                    variant="caption"
                    className="scroll-lock-pause"
                    fontSize="0.8rem"
                    onMouseEnter={() => {
                      clearTimeout(unlockViewTimerRef.current);
                      setIsViewLocked(true);
                    }}
                    onMouseLeave={() => {
                      unlockViewTimerRef.current = setTimeout(() => {
                        setIsViewLocked(false);
                      }, 4000);
                    }}
                    style={{
                      cursor: "pointer",
                      fontSize: "0.8rem",
                      color: "gray",
                      userSelect: "none",
                    }}
                  >
                    Hover here to pause scrolling to last filled blank
                  </Typography>

                  {currentSectionConfig.type === "fill-in" &&
                  questions.length > 0 ? (
                    <QuestionFillAnimation
                      key={`${selectedSection}-${selectedPart}`}
                      data={questions}
                      activeQuestionIndex={currentQuestion}
                      hintuse={true}
                      sparseFlashyToggle={isSparseFlashyToggle}
                      isViewLocked={isViewLocked}
                      animationIntervals={
                        currentSectionConfig?.animationInterval_speed_of__parts_1based
                      }
                      currentPartNumber={parseInt(selectedPart, 10)}
                    />
                  ) : (
                    <Typography>MCQ Quiz Area</Typography>
                  )}

                  <Box mt={4} display="flex" justifyContent="space-between">
                    <Button
                      onClick={handlePrevious}
                      disabled={currentQuestion === 0}
                      variant="contained"
                      color="primary"
                      sx={{ textTransform: "none" }}
                    >
                      Previous
                    </Button>
                    <Button
                      onClick={handleNext}
                      disabled={currentQuestion === questions.length - 1}
                      variant="contained"
                      color="primary"
                      sx={{ textTransform: "none" }}
                    >
                      Next
                    </Button>
                  </Box>
                </CardContent>
              </Card>

              <Box
                display="flex"
                justifyContent="space-between"
                mt={0.2}
                width={{ xs: "95%", sm: "80%", md: "700px" }}
                maxWidth="90vw"
              >
                <Button
                  variant="contained"
                  color="secondary"
                  onClick={handleReturnToPart}
                  sx={{ textTransform: "none" }}
                >
                  Select Another Part
                </Button>
                <Button
                  variant="contained"
                  color="secondary"
                  onClick={handleReturnToSection}
                  sx={{ textTransform: "none" }}
                >
                  Select Another Group
                </Button>
              </Box>
            </>
          )}
        </Box>
      </motion.div>
    </ThemeProvider>
  );
}

export default App;
