// src/CodeHighlighter.js
import React from 'react';
import { Highlight, themes } from 'prism-react-renderer';
import { useTheme } from "./ThemeContext";
import './CodeHighlighter.css';

// Helper function for grey comments (remains unchanged)
const createThemeWithGreyComments = (baseTheme) => {
  return {
    ...baseTheme,
    styles: [
      ...baseTheme.styles,
      {
        types: ['comment', 'prolog', 'doctype', 'cdata'],
        style: { color: 'slategray', fontStyle: 'italic' },
      },
    ],
  };
};

// --- NEW ---
// Regex to find markers. Captures the text before (group 1) and the ID (group 2).
const bfMarkerRegex = /^(.*)_BF_(\d+)$/;

const CodeHighlighter = ({ code, language, inline = false }) => {
    const { mode } = useTheme();
    const baseTheme = mode === 'dark' ? themes.vsDark : themes.github;
    const theme = createThemeWithGreyComments(baseTheme);

    if (!code) {
        return null;
    }

    const HighlightedContent = ({ className, style, tokens, getLineProps, getTokenProps }) => {
        // --- MODIFIED: We apply the same logic to both inline and block modes ---
        const renderTokens = (lineTokens) => {
            return lineTokens.map((token, key) => {
                const tokenProps = getTokenProps({ token, key });
                let tokenContent = token.content;
                let customStyle = {};
                let customProps = {}; // For our data-* attribute

                const match = token.content.match(bfMarkerRegex);
                if (match) {
                    tokenContent = match[1]; // Cleaned text (e.g., "server")
                    const bfId = match[2];     // The number (e.g., "2")
                    
                    // Add a data attribute so we can find this element later
                    customProps['data-bf-id'] = bfId;

                    // Optional: Add a subtle highlight to the tagged word
                    customStyle = {
                        backgroundColor: 'rgba(255, 230, 100, 0.2)',
                        borderRadius: '3px',
                    };
                }

                return (
                    <span 
                        key={key} 
                        {...tokenProps} 
                        {...customProps} // Spread the custom props (data-bf-id)
                        style={{ ...tokenProps.style, ...customStyle }}
                    >
                        {tokenContent}
                    </span>
                );
            });
        };

        if (inline) {
            return (
                <span className={className} style={{...style, display: 'inline', backgroundColor: 'transparent'}}>
                    {renderTokens(tokens[0])}
                </span>
            );
        }

        return (
            <pre className={`${className} code-block-container`} style={{ ...style, display: 'inline-block', width: '100%' }}>
                {tokens.map((line, i) => (
                    <div key={i} {...getLineProps({ line, key: i })} className="code-line">
                        <span className="line-number">{i + 1}</span>
                        <span className="line-content">
                            {renderTokens(line)}
                        </span>
                    </div>
                ))}
            </pre>
        );
    };

    return (
        <Highlight 
            theme={theme} 
            code={code}
            language={language || 'jsx'}
        >
            {HighlightedContent}
        </Highlight>
    );
};

export default CodeHighlighter;