import React, { useEffect, useState, useMemo } from "react";
import { catmullRom2Bezier, parseTextToStyledChars } from "./utils"; // MODIFICATION: Import the new parser
import RichTextRenderer from './RichTextRenderer';

// --- Configuration ---
const DEFAULT_DURATION_MS = 6200;
const CURVE_TENSION = 4.3;
const CURVE_VERTICAL_OFFSET = -90;
const LETTER_STAGGER_MS = 50;
const CROSSFADE_DURATION_MS = 300;

// --- Helper Function ---
const generateCurvePoints = (startX, startY, endX, endY, verticalOffset) => {
    const dx = endX - startX;
    if (
        typeof startX !== "number" ||
        typeof startY !== "number" ||
        typeof endX !== "number" ||
        typeof endY !== "number"
    ) {
        return [];
    }
    return [{
        x: startX,
        y: startY
    }, {
        x: startX + dx * Math.random() + 0.15,
        y: Math.min(startY, endY) + verticalOffset,
    }, {
        x: startX + dx * Math.random() + 0.5,
        y: Math.min(startY, endY) + verticalOffset,
    }, {
        x: endX,
        y: endY
    }, ];
};

function AnimatedWord({
    text = "",
    initialX = 0,
    initialY = 0,
    targetX = null,
    targetY = null,
    startDelay = 0,
    animationType = "svg-curve",
    duration = DEFAULT_DURATION_MS,
    textColor = "DodgerBlue" ,
    fontSize = "18px",
    letterStagger = LETTER_STAGGER_MS,
    
    onAnimationEnd = () => {},
}) {
    const [isAnimating, setIsAnimating] = useState(false);
    const [isFinishing, setIsFinishing] = useState(false);
    const [isVisible, setIsVisible] = useState(false);

    const hasValidTarget =
        typeof targetX === "number" && typeof targetY === "number";
    const hasValidInitial =
        typeof initialX === "number" && typeof initialY === "number";
    const isCurveType = animationType === "svg-curve";

    // MODIFICATION: Use the new parser to create styled character objects
    const characters = useMemo(() => {
        const styledChars = parseTextToStyledChars(text);
        // Reverse for the animation effect
        return styledChars.reverse();
    }, [text]);


    // Effect to start the animation visibility and timing
    useEffect(() => {
        if (!hasValidTarget || !hasValidInitial) {
            setIsAnimating(false);
            setIsFinishing(false);
            setIsVisible(false);
            return;
        }
        setIsVisible(true);
        const startAnimTimer = setTimeout(() => {
            setIsAnimating(true);
        }, startDelay);

        return () => clearTimeout(startAnimTimer);
    }, [startDelay, hasValidTarget, hasValidInitial]);

    // Effect to call the onAnimationEnd callback when the animation duration is over
    useEffect(() => {
        if (!isAnimating) return;

        let totalDuration = duration;
        if (animationType === "svg-curve") {
            const lastLetterStartTime = (characters.length - 1) * letterStagger;
            totalDuration = lastLetterStartTime + duration;
        }

        const endTimer = setTimeout(() => {
            onAnimationEnd();
        }, totalDuration);

        return () => clearTimeout(endTimer);
    }, [
        isAnimating,
        duration,
        animationType,
        characters,
        letterStagger,
        onAnimationEnd,
    ]);

    // Effect to handle the cross-fade between the letter animation and the final word
    useEffect(() => {
        if (!isAnimating || !isCurveType || characters.length === 0) {
            setIsFinishing(false);
            return;
        }

        const lastLetterAnimEndTime =
            (characters.length - 1) * letterStagger + duration;

        const startCrossfadeDelay = Math.max(
            0,
            lastLetterAnimEndTime - CROSSFADE_DURATION_MS / 2
        );

        const finishTimer = setTimeout(() => {
            setIsFinishing(true);
        }, startCrossfadeDelay);

        return () => clearTimeout(finishTimer);
    }, [isAnimating, isCurveType, characters.length, letterStagger, duration]);

    // Memoized calculation for the SVG animation path
    const pathData = useMemo(() => {
        if (!isCurveType || !hasValidInitial || !hasValidTarget) return null;
        try {
            const points = generateCurvePoints(
                initialX,
                initialY,
                targetX,
                targetY,
                CURVE_VERTICAL_OFFSET
            );
            return points?.length >= 2 ?
                catmullRom2Bezier(points, CURVE_TENSION) :
                null;
        } catch (error) {
            console.error("Error generating Catmull-Rom path:", error);
            return null;
        }
    }, [
        isCurveType,
        initialX,
        initialY,
        targetX,
        targetY,
        hasValidInitial,
        hasValidTarget,
    ]);

    // MODIFICATION: The function now accepts and applies a custom style per character
    const getAnimatingLetterStyle = (index, customStyle = {}) => {
        if (!isCurveType) return {};

        const letterStartDelayMs = index * letterStagger;

        let style = {
            ...customStyle,
            position: "absolute",
            left: "0px",
            top: "0px",
            fontSize: fontSize,
            color: customStyle.color || textColor,
            whiteSpace: "pre",
            display: "inline-block",
            willChange: "offset-distance, opacity",
            zIndex: 10,
            visibility: isVisible ? "visible" : "hidden",
            opacity: 0,
            offsetPath: pathData ? `path('${pathData}')` : "none",
            offsetDistance: "0%",
            transitionProperty: "offset-distance, opacity",
            transitionDuration: `${duration / 1000}s, ${duration / 1500}s`,
            transitionTimingFunction: `cubic-bezier(${Math.random()}, ${Math.random()}, ${Math.random()}, ${Math.random()}, ${Math.random()}), ease-out`,
            transitionDelay: `${letterStartDelayMs / 1000}s`,
        };

        if (!pathData) {
            return { ...style, opacity: 0.5, visibility: "hidden" };
        }

        if (isAnimating) {
            style.opacity = 1;
            style.offsetDistance = "100%";
        }

        if (isFinishing) {
            style.opacity = 0;
            style.transitionProperty = "opacity";
            style.transitionDuration = `${CROSSFADE_DURATION_MS / 1000}s`;
            style.transitionTimingFunction = "ease-out";
            style.transitionDelay = "0s";
        }

        return style;
    };

    // Memoized style for the final, static word that appears after the SVG animation
    const finalWordStyle = useMemo(() => {
        if (!isCurveType) return {};

        return {
            position: "absolute",
            marginLeft: "3px",
            padding: "0 0px",
            lineHeight: "1.72",
            left: hasValidTarget ? `${targetX}px` : "-9999px",
            top: hasValidTarget ? `${targetY}px` : "-9999px",
            fontSize: fontSize,
            color: textColor,
            whiteSpace: "nowrap",
            opacity: isFinishing ? 1 : 0,
            visibility: isVisible ? "visible" : "hidden",
            zIndex: 9,
            willChange: "opacity",
            transition: `opacity ${CROSSFADE_DURATION_MS / 1000}s ease-in`,
        };
    }, [
        isCurveType,
        isFinishing,
        isVisible,
        hasValidTarget,
        targetX,
        targetY,
        fontSize,
        textColor,
    ]);

    // Memoized style for all non-SVG path animations (pop, spring, etc.)
    const nonCurveWordStyle = useMemo(() => {
        if (isCurveType) return {};
        let style = {
            position: "absolute",
            fontSize: fontSize,
            color: textColor,
            whiteSpace: "nowrap",
            willChange: "transform, opacity, top, left",
            transition: `all ${duration / 1000}s ease-out`,
            opacity: isVisible ? 1 : 0,
            zIndex: 10,
            visibility: isVisible ? "visible" : "hidden",
        };
        if (!hasValidInitial || !hasValidTarget) {
            return { ...style, opacity: 0, visibility: "hidden", left: "-9999px" };
        }
        switch (animationType) {
            case "pop-up":
                style.left = `${targetX}px`;
                style.top = `${targetY}px`;
                style.transform = isAnimating ? "scale(1)" : "scale(0.3)";
                style.opacity = isAnimating ? 1 : 0;
                style.transition = `transform ${
          duration / 1000
        }s cubic-bezier(${Math.random()}, ${Math.random()}, ${Math.random()}, ${Math.random()}), opacity ${
          duration / 1000
        }s ease-out`;
                break;
            case "fly-in":
                style.left = isAnimating ? `${targetX}px` : `${initialX}px`;
                style.top = isAnimating ? `${targetY}px` : `${initialY}px`;
                style.opacity = isAnimating ? 1 : 0;
                style.transition = `left ${duration / 1000}s ease-out, top ${
          duration / 1000
        }s ease-out, opacity ${duration / 1000}s ease-out`;
                break;
            case "spring": // This ensures spring animation works
            case "spring-in":
                style.left = `${targetX}px`;
                style.top = `${targetY}px`;
                style.transform = isAnimating ? "scale(1)" : "scale(0.1)";
                style.opacity = isAnimating ? 1 : 0;
                style.transition = `transform ${
          duration / 1000
        }s cubic-bezier(0.175, 0.885, 0.32, 1.5), opacity ${
          duration / 1000
        }s ease-out`;
                break;
            default: // Fallback to a simple fade
                style.left = `${targetX}px`;
                style.top = `${targetY}px`;
                style.opacity = isAnimating ? 1 : 0;
                style.transition = `opacity ${duration / 1000}s ease-in`;
                break;
        }
        return style;
    }, [
        isCurveType,
        isAnimating,
        isVisible,
        hasValidInitial,
        hasValidTarget,
        animationType,
        duration,
        fontSize,
        textColor,
        initialX,
        initialY,
        targetX,
        targetY,
    ]);


    if (!isVisible) {
        return null;
    }

    if (isCurveType) {
        return (
            <>
                {pathData &&
                // MODIFICATION: The map function now destructured {char, style}
                characters.map(({ char, style }, index) => (
                    <span key={`anim-${index}`} style={getAnimatingLetterStyle(index, style)}>
                    {char === " " ? "\u00A0" : char}
                    </span>
                ))}
                
                {/* The landed word uses RichTextRenderer to ensure final state is styled */}
                {pathData && 
                    <div style={finalWordStyle}>
                        <RichTextRenderer text={text} />
                    </div>
                }

                {/* The fallback also uses RichTextRenderer */}
                {!pathData && hasValidTarget && (
                <div
                    style={{
                    position: "absolute",
                    left: `${targetX}px`,
                    top: `${targetY}px`,
                    fontSize,
                    color: textColor,
                    opacity: isAnimating ? 1 : 0,
                    transition: "opacity 0.3s",
                    }}
                >
                    <RichTextRenderer text={text} /> <span>(Path Failed)</span>
                </div>
                )}
            </>
        );
    }

    // All other animation types also use RichTextRenderer for their final state
    return <div style={nonCurveWordStyle}><RichTextRenderer text={text} /></div>;
}

export default AnimatedWord;