import React, {
  createContext,
  useState,
  useMemo,
  useContext,
  useEffect,
} from "react";

// Create the context with a default value
const ThemeContext = createContext({
  toggleTheme: () => {},
  // You can change this too for consistency, though it has less impact
  mode: "dark", 
});

// Custom hook for easy consumption of the context
export const useTheme = () => useContext(ThemeContext);

// The provider component that will wrap your app
export const CustomThemeProvider = ({ children }) => {
  // State to hold the current theme mode. We read from localStorage to keep the user's preference.
  const [mode, setMode] = useState(
    localStorage.getItem("themeMode") || "dark" // <-- THE FIX IS HERE
  );

  // Persist the theme mode to localStorage whenever it changes.
  useEffect(() => {
    localStorage.setItem("themeMode", mode);
  }, [mode]);

  // The function to toggle the theme
  const toggleTheme = () => {
    setMode((prevMode) => (prevMode === "light" ? "dark" : "light"));
  };

  // We use useMemo to prevent re-creating the value object on every render
  const themeContextValue = useMemo(
    () => ({
      mode,
      toggleTheme,
    }),
    [mode]
  );

  return (
    <ThemeContext.Provider value={themeContextValue}>
      {children}
    </ThemeContext.Provider>
  );
};