import React, { useState, useEffect, useRef } from "react";
import SingleQuestionAnimation from "./SingleQuestionAnimation";

const PAUSE_BEFORE_LOOP_MS = 2500;

const QuestionFillAnimation = ({
  data,
  activeQuestionIndex,
  hintuse,
  sparseFlashyToggle,
  isViewLocked,
  animationIntervals,
  currentPartNumber // --- CHANGE: Accept the new prop ---
}) => {
  const [animationTrigger, setAnimationTrigger] = useState(0);
  const loopTimerRef = useRef(null);

  useEffect(() => {
    clearTimeout(loopTimerRef.current);
    setAnimationTrigger(0);

    return () => {
      clearTimeout(loopTimerRef.current);
    };
  }, [activeQuestionIndex]);

  if (!Array.isArray(data) || data.length === 0) {
    return <div>No valid question data provided.</div>;
  }

  if (activeQuestionIndex < 0 || activeQuestionIndex >= data.length) {
    return <div>Invalid question index provided.</div>;
  }

  const handleAllAnimationsComplete = () => {
    loopTimerRef.current = setTimeout(() => {
      setAnimationTrigger((prev) => prev + 1);
    }, PAUSE_BEFORE_LOOP_MS);
  };

  const currentQuestionData = data[activeQuestionIndex];

  return (
    <div>
      <SingleQuestionAnimation
        key={`${activeQuestionIndex}-${animationTrigger}`}
        data={currentQuestionData}
        hintuse={hintuse}
        sparseFlashyToggle={sparseFlashyToggle}
        onAllAnimationsComplete={handleAllAnimationsComplete}
        isViewLocked={isViewLocked}
        animationIntervals={animationIntervals}
        currentPartNumber={currentPartNumber} // --- CHANGE: Pass it down ---
      />
    </div>
  );
};

export default QuestionFillAnimation;