// src/utils.js

/**
 * Creates a smooth SVG path data string (d attribute) using Catmull-Rom to Cubic Bezier conversion.
 * @param {Array<{x: number, y: number}>} pts - Array of points the curve should pass through/near.
 * @param {number} [tension=0.5] - Controls the tightness of the curve. 0 is linear, 1 is tighter.
 * @returns {string} The SVG path data string.
 */
export function catmullRom2Bezier(pts, tension = 0.5) {
  if (!pts || pts.length < 2) return "";

  let d = `M${pts[0].x},${pts[0].y}`; // Start moveTo

  // Iterate through the segments of the curve
  for (let i = 0; i < pts.length - 1; i++) {
    // Determine control points p0, p1, p2, p3 for the Catmull-Rom spline segment
    // Handle boundary conditions: if points don't exist, reuse adjacent points
    const p0 = i === 0 ? pts[i] : pts[i - 1];
    const p1 = pts[i];
    const p2 = pts[i + 1];
    const p3 = i + 2 < pts.length ? pts[i + 2] : p2; // Reuse p2 if p3 is out of bounds

    // Calculate the cubic Bezier control points (cp1, cp2) for the segment from p1 to p2
    // Formula derived from the Catmull-Rom spline definition
    const cp1x = p1.x + ((p2.x - p0.x) * tension) / 6;
    const cp1y = p1.y + ((p2.y - p0.y) * tension) / 6;

    const cp2x = p2.x - ((p3.x - p1.x) * tension) / 6;
    const cp2y = p2.y - ((p3.y - p1.y) * tension) / 6;

    // Append the cubic Bezier segment to the path data string
    d += `C${cp1x},${cp1y} ${cp2x},${cp2y} ${p2.x},${p2.y}`;
  }
  return d;
}

// Helper function to generate intermediate wavy points
export function generateWavyPoints(p1, p2, numPoints = 3, amplitude = 40) {
  const intermediatePoints = [];
  const deltaX = p2.x - p1.x;
  const deltaY = p2.y - p1.y;
  const totalDistance = Math.sqrt(deltaX * deltaX + deltaY * deltaY);

  // Calculate a normalized perpendicular vector
  let perpX = -deltaY;
  let perpY = deltaX;
  if (totalDistance > 0) {
      const len = Math.sqrt(perpX * perpX + perpY * perpY);
      if (len > 0) { perpX /= len; perpY /= len; }
      else { perpX = 0; perpY = 0; }
  } else {
      perpX = 0; perpY = 0;
  }

  for (let i = 1; i <= numPoints; i++) {
      const fraction = i / (numPoints + 1);
      const lineX = p1.x + fraction * deltaX;
      const lineY = p1.y + fraction * deltaY;
      const currentAmplitude = amplitude * Math.sin(fraction * Math.PI); // Sine variation
      const sign = (i % 2 === 1) ? 1 : -1; // Alternate offset direction
      const offsetX = sign * currentAmplitude * perpX;
      const offsetY = sign * currentAmplitude * perpY;
      intermediatePoints.push({ x: lineX + offsetX, y: lineY + offsetY });
  }
  return intermediatePoints;
}

export function interpolatePathPoints(points, steps = 100) {
  const result = [];
  for (let i = 0; i <= steps; i++) {
    const t = i / steps;
    const x = getPointOnCurve(points, t, "x");
    const y = getPointOnCurve(points, t, "y");
    result.push({ x, y });
  }
  return result;
}

// Very basic linear Catmull-Rom approximation
export function getPointOnCurve(points, t, key) {
  const n = points.length - 1;
  const index = Math.min(Math.floor(t * n), n - 1);
  const localT = (t * n) - index;

  const p0 = points[index];
  const p1 = points[index + 1];
  return p0[key] + (p1[key] - p0[key]) * localT;
}

// --- UPGRADED UTILITIES FOR RICH TEXT PARSING ---

const PARSE_REGEX = /<span\s+style=\{\{(.*?)\}\}>(.*?)<\/span>|(\p{Emoji_Presentation}|\p{Emoji}\uFE0F)/gu;

/**
 * Parses a JSX-style style string into a React style object, now with gradient support.
 * @param {string} styleStr The string content from the style attribute.
 * @returns {object} A valid React style object.
 */
export function parseStyleString(styleStr) {
  const styleObject = {};
  if (!styleStr) return styleObject;

  // Use a regex to find key-value pairs to handle complex values like gradients
  const styleRegex = /(\w+)\s*:\s*'(.*?)'/g;
  let match;
  let hasGradient = false;

  while ((match = styleRegex.exec(styleStr)) !== null) {
    const key = match[1].trim();
    const value = match[2].trim();

    // The "magic" gradient handling
    if (key === 'gradient') {
      hasGradient = true;
      styleObject.backgroundImage = `linear-gradient(${value})`;
      styleObject.WebkitBackgroundClip = 'text'; // For cross-browser compatibility
      styleObject.backgroundClip = 'text';
    } else {
      styleObject[key] = value;
    }
  }

  // If a gradient was applied, the text color MUST be transparent for it to show
  if (hasGradient) {
    styleObject.color = 'transparent';
  }

  return styleObject;
};

export function parseTextToStyledChars(text) {
    const styledChars = [];
    if (!text) return styledChars;
    let lastIndex = 0;
    let match;
    while ((match = PARSE_REGEX.exec(text)) !== null) {
        if (match.index > lastIndex) {
            const plainText = text.substring(lastIndex, match.index);
            Array.from(plainText).forEach(char => styledChars.push({ char, style: {} }));
        }
        const [fullMatch, styleString, innerText, emoji] = match;
        if (styleString !== undefined && innerText !== undefined) {
            const style = parseStyleString(styleString);
            Array.from(innerText).forEach(char => styledChars.push({ char, style }));
        }
        else if (emoji) {
            styledChars.push({ char: emoji, style: {} });
        }
        lastIndex = PARSE_REGEX.lastIndex;
    }
    if (lastIndex < text.length) {
        const remainingText = text.substring(lastIndex);
        Array.from(remainingText).forEach(char => styledChars.push({ char, style: {} }));
    }
    return styledChars;
};

/**
 * Strips custom span tags from a string to get the plain text representation for sizing.
 * @param {string} richText The string containing rich text tags.
 * @returns {string} The plain text version of the string.
 */
export function stripCustomTags(richText) {
  if (!richText) return '';
  // This regex finds our specific <span style={{...}}>...</span> tags and replaces them with their content.
  return richText.replace(/<span\s+style=\{\{.*?\}\}>(.*?)<\/span>/g, '$1');
}